import React, {Component,Fragment} from 'react';
import { Link } from 'react-router-dom';
import {Consumer} from '../../store/index';
import Hero from '../layout/hero';
import Banner from '../layout/banner';
import  StyledHero from '../layout/styledHero';
import defaultBg from '../images/room-1.jpeg';

class SingleRoom extends Component  {
  //constructor
  constructor(props) {
    super(props)
    this.state = {
      slug: this.props.match.params.slug,
      defaultBg
    }
  }
  // render dom
  render(){
    return (
      <Consumer>
        {value => {
          const {rooms} = value;
          let room = rooms.find(room =>room.slug === this.state.slug);

          if(!room) {
            return <div className="error">
              <h3>No such room could not found</h3>
              <Link to="/rooms" className="btn-primary">
                back to rooms
              </Link>
            </div>
          }
          else {
            const {name,description,capacity,size,price,extras,breakfast,pets,images} = room;
            const [mainImg,...defaultImg] = images;
            return (
              <Fragment>
                <StyledHero img={mainImg || this.state.defaultBg}>
                  <Banner title={`${name} room`}>
                    <Link  to='/rooms' className="btn-primary">
                      back to rooms
                    </Link>
                  </Banner>
                </StyledHero>
                <section className="single-room">
                  <div className="single-room-images">
                    {
                      defaultImg.map((item,index)=>{
                        return <img src={item} key={index} alt={name}/>
                      })
                    }
                  </div>
                  <div className="single-room-info">
                    <article className="desc">
                      <h3>Details</h3>
                      
                      <p>{description}</p>
                    </article>
                    <article className="info">
                      <h3>Info</h3>
                      <h6>Price: ${price}</h6>
                      <h6>size: ${size} SQFT</h6>
                      <h6>
                        max-capacity: {' '} 
                        {
                          capacity > 1 ? `${capacity} people` : `${capacity} person`
                        }
                      </h6>
                      <h6>
                        {
                          pets ? `pets allowed` : `no pets allowed`
                        }
                      </h6>
                      <h6>
                        {
                          breakfast && 'free breakfast included'
                        }
                      </h6>
                    </article>
                  </div>
                </section>
                <section className="room-extras">
                  <h6>extras</h6>
                  <ul className="extras">
                    {
                      extras.map((item,index)=> {
                      return <li key={index}>-{item}</li>
                      })
                    }
                  </ul>
                </section>
              </Fragment>

            )
          }
        }}
      </Consumer>
    );
  }
};

export default  SingleRoom;
